export default{
	props:{
		messagedata:Array,
		tabmessage:Array,
		classmessage:Array
	},
	data() {
		return {
			num:0,
			// 评论内容
			messagelist: [],
		}
	},
	
	methods:{
		// 评价分类
		menubtn(index,item){
			this.num = index
			// 子组件调用父组件某个函数去发送请求
			this.$parent.messageMethod(item);
		}
	},
	// 监听变化
	watch: {
		messagedata(newValue, oldValue) {
			this.messagelist= newValue.map(v=>v.messagedata)
			this.messagelist.reverse()
		}
	}
	
}
